import config from 'config';
import hcRequest from '../lib/hcRequest';

const documentRoutes = {

    getFileDownloadURL(ownerId, recordId, fileId) {
        return hcRequest.submit('GET', `${config.apiUrl()}/users/${ownerId}/documents/${recordId}/files/${fileId}/download_access_token`, { authorize: true, ownerId });
    },

    getFileUploadURLs(ownerId, recordId, fileNumber) {
        const body = { file_number: fileNumber };
        return hcRequest.submit('POST', `${config.apiUrl()}/users/${ownerId}/documents/${recordId}/tokens`, { body, authorize: true, ownerId });
    },

    createRecord(ownerId, data) {
        return hcRequest.submit('POST', `${config.apiUrl()}/users/${ownerId}/records`, { body: data, authorize: true, ownerId });
    },

    updateRecord(ownerId, recordId, data) {
        return hcRequest.submit('PUT', `${config.apiUrl()}/users/${ownerId}/records/${recordId}`, { body: data, authorize: true, ownerId });
    },

    searchRecords(ownerId, queryParams) {
        return hcRequest.submit('GET', `${config.apiUrl()}/users/${ownerId}/records`, {
            query: queryParams,
            authorize: true,
            includeResponseHeaders: true,
            ownerId,
        })
            .then(({ body, headers }) => ({ records: body, totalCount: headers['x-total-count'] }));
    },

    getRecordsCount(ownerId, queryParams) {
        return hcRequest.submit('HEAD', `${config.apiUrl()}/users/${ownerId}/records`, {
            query: queryParams,
            authorize: true,
            includeResponseHeaders: true,
            ownerId,
        })
            .then(({ headers }) => ({ totalCount: headers['x-total-count'] }));
    },

    downloadRecord(ownerId, recordId) {
        return hcRequest.submit('GET', `${config.apiUrl()}/users/${ownerId}/records/${recordId}`, { authorize: true, ownerId });
    },

    deleteRecord(ownerId, recordId) {
        return hcRequest.submit('DELETE', `${config.apiUrl()}/users/${ownerId}/records/${recordId}`, { authorize: true, ownerId });
    },

    fetchAttachmentKey(ownerId, recordId) {
        return hcRequest.submit('GET', `${config.apiUrl()}/users/${ownerId}/records/${recordId}/attachment_key`, { authorize: true, ownerId });
    },
};

export default documentRoutes;



