import fhirValidator from '../lib/fhirValidator';
import documentRoutes from '../routes/documentRoutes';
import userService from './userService';
import taggingUtils from '../lib/taggingUtils';
import dateUtils from '../lib/dateUtils';
import hcCrypto from '../lib/crypto';
import createCryptoService from './cryptoService';

const fhirService = {
    /**
     * @param {string} ownerId
     * @param {Object} record
     * @param {string} record.id
     * @param {Object} record.fhirResource The Fhir Resource contained in record
     * @param {Array<string>} record.tags
     * @param {string} record.attachmentKey
     * @param {Date} record.customCreationDate The custom date entered by user
     * @param {Date} record.updatedDate The date of update of record
     * @returns {Promise<Object>}
     */
    updateFhirRecord(ownerId, record) {
        const updateRequest =
            (userId, params) => documentRoutes.updateRecord(userId, record.id, params);

        return this.uploadFhirRecord(
            ownerId, record, updateRequest);
    },

    /**
     * @param {string} ownerId
     * @param {Object} record
     * @returns {Promise<Object>}
     */
    createFhirRecord(ownerId, record) {
        return this.uploadFhirRecord(ownerId, record, documentRoutes.createRecord);
    },
    /**
     * @param {string} ownerId
     * @param {Object} record
     * @param {string} record.id
     * @param {Object} record.fhirResource The Fhir Resource contained in record
     * @param {Array<string>} record.tags
     * @param {string} record.attachmentKey
     * @param {Date} record.customCreationDate The custom date entered by user
     * @param {Date} record.updatedDate The date of update of record
     * @returns {Promise<Object>}
     */
    uploadFhirRecord(ownerId, record, uploadRequest) {
        return fhirValidator.validate(record.fhirResource)
            .then(() => {
                record.tags = [
                    ...(record.tags || []),
                    ...taggingUtils.generateTags(record.fhirResource),
                ];
                return this.uploadRecord(
                    ownerId,
                    record,
                    uploadRequest);
            });
    },
    /**
     * @param {string} ownerId
     * @param {Object} record
     * @param {string} record.id
     * @param {Object} record.fhirResource The Fhir Resource contained in record
     * @param {Array<string>} record.tags
     * @param {string} record.attachmentKey
     * @param {Date} record.customCreationDate The custom date entered by user
     * @param {Date} record.updatedDate The date of update of record
     * @returns {Promise<Object>}
     */
    uploadRecord(ownerId, record, uploadRequest) {
        let owner;
        return userService.getUser(ownerId)
            .then((user) => {
                owner = user;
                return Promise.all([
                    createCryptoService(owner.id).encryptObject(record.fhirResource),
                    Promise.all(record.tags.map(tag => hcCrypto.symEncryptString(owner.tek, tag))),
                ]);
            })
            .then(([[cipherData, cipherKey], encryptedTags]) => {
                const params = {
                    encrypted_body: cipherData,
                    encrypted_key: cipherKey,
                    encrypted_tags: encryptedTags,
                    date: dateUtils.formatDateYyyyMmDd(record.customCreationDate || new Date()),
                    attachment_key: record.attachmentKey,
                    model_version: DATA_MODEL_VERSION,
                };
                return uploadRequest(owner.id, params);
            })
            .then(result => ({
                fhirResource: record.fhirResource,
                tags: record.tags,
                customCreationDate: result.date,
                updatedDate: result.createdAt,
                record_id: result.record_id,
            }));
    },

    downloadFhirRecord(ownerId, recordId) {
        return documentRoutes.downloadRecord(ownerId, recordId)
            .then(result => userService.getUser(ownerId)
                .then(user => this.decryptRecordAndTags(result, user.tek)));
    },

    searchRecords(ownerId, params, countOnly = false) {
        let user;
        let totalCount;

        return userService.getUser(ownerId)
            .then((userObject) => {
                user = userObject;
                if (params.client_id) {
                    const clientTag = taggingUtils.buildTag('client', params.client_id);
                    params.tags = params.tags ?
                        [...params.tags, clientTag] :
                        [clientTag];
                    delete params.client_id;
                }

                if (params.tags) {
                    return Promise.all(params.tags
                        .map(tag => hcCrypto.symEncryptString(user.tek, tag)))
                        .then(encryptedTags => encryptedTags.join(','))
                        .then((tags) => {
                            params.tags = tags;
                            return params;
                        });
                }
                return params;
            })
            .then((queryParams) => {
                if (countOnly) {
                    return documentRoutes.getRecordsCount(user.id, queryParams);
                }
                return documentRoutes.searchRecords(user.id, queryParams);
            })
            .then((searchResult) => {
                totalCount = searchResult.totalCount;
                return searchResult.records
                    ? Promise.all(searchResult.records.map(result =>
                        this.decryptRecordAndTags(result, user.tek)
                            .catch((err) => {
                                console.warn(`Decryption failed for record: ${result.record_id}`, err);
                                return new Error(err);
                            })))
                    : undefined;
            })
            .then(results => (results
                ? { totalCount, records: results.filter(result => !(result instanceof Error)) }
                : { totalCount }));
    },

    deleteRecord(ownerId, recordId) {
        return documentRoutes.deleteRecord(ownerId, recordId);
    },

    decryptRecordAndTags(record, tagKey) {
        const tagsPromise = Promise.all(record.encrypted_tags.map(tag =>
            hcCrypto.symDecryptString(tagKey, tag)));

        const recordPromise = createCryptoService(record.user_id)
            .decryptData(
                record.encrypted_key,
                hcCrypto.convertBase64ToArrayBufferView(record.encrypted_body))
            .then(hcCrypto.convertArrayBufferViewToString)
            .then(JSON.parse);
        return Promise.all([
            recordPromise,
            tagsPromise,
        ])
            .then(results => ({
                fhirResource: results[0],
                tags: results[1],
                updatedDate: new Date(record.createdAt),
                customCreationDate: new Date(record.date),
                record_id: record.record_id,
                attachment_key: record.attachment_key,
            }));
    },
};

export default fhirService;



